.about-us {
  padding-top: 20px;
  padding-bottom: 20px;
  color: @main;
  background-color: @secondary;

  @media (min-width: @desktop-width) {
    margin-bottom: 50px;
  }
}

.about-us__wrapper {
  @media (min-width: @tablet-width) {
    position: relative;
    width: 60vw;
    margin: 0 auto;
    padding-left: 250px;
  }

  @media (min-width: @desktop-width) {
    width: 50%;
    padding-left: 400px;
  }
}

.about-us__wrapper::before {
  @media (min-width: @tablet-width) {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    background-image: url("../img/about-us__logo.png");
    background-repeat: no-repeat;
    background-size: contain;
  }

  @media (min-width: @desktop-width) {
    right: 100px;
  }
}

.about-us__title {
  font-size: 22px;
  font-weight: bold;
  margin: 10px 0 20px;

  @media (min-width: @desktop-width) {
    font-size: 28px;
  }
}

.about-us__info {
  font-size: 14px;

  @media (min-width: @desktop-width) {
    font-size: 24px;
  }
}

.about-us__important {
  font-weight: bold;
  color: @base;
}
