.production__title {
  font-size: 18px;
  color: @main;
  text-align: center;

  @media (min-width: @tablet-width) {
    font-size: 28px;
  }
}

.prodution__list {
  margin: 0 0 20px;
  padding: 0;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
    margin: 0 0 30px;
  }
}

.production__item {
  display: flex;
  flex-direction: column;
  position: relative;
  background-color: @main;
  margin-bottom: 20px;
  padding-bottom: 10px;

  @media (min-width: @tablet-width) {
    border: 3px solid transparent;
    border-image: linear-gradient(to left top, @base 10%, @main 30%, @main 70%, @base 90%);
    border-image-slice: 1;
    margin: 0;
  }

  &:hover {
    border: 3px solid @base;
  }

  &:active {
    border: 3px solid @base;
    box-shadow: 0 0 10px @main;
    opacity: 0.6;
  }
}

.production__link {
  text-decoration: none;
}

.production__item::before {
  content: "от 999999";
  position: absolute;
  top: 20px;
  left: 20px;
  font-weight: bold;
  color: @base;
  min-width: 70px;
  max-width: 85%;
  background-color: @main;
  border-radius: 7px;
  padding: 5px;
}

.production__item-title {
  font-size: 22px;
  color: @base;
  font-weight: bold;
  text-align: center;
  margin: 10px 0;
  padding: 0 20px;
}

.production__item-image {
  display: block;
  width: 100%;
  object-fit: cover;
  overflow: hidden;
}

.production__item-option {
  display: block;
  color: @text-color;
  margin: 5px 0;
  padding: 0 20px;

  @media (min-width: @tablet-width) {
    margin: 10px 0;
  }
}
