.contacts {
  max-width: 1240px;
  margin: 0 auto;
  padding: 0;
  color: @main;
  text-align: center;
}

.contacts__wrapper {
  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: repeat(2, 50%);
  }
}

.contacts__info {
  font-size: 18px;
  padding: 20px 20px 30px;

  @media (min-width: @desktop-width) {
    font-size: 28px;
    padding-top: 80px;
  }
}

.contacts__order {
  font-weight: bold;
  font-size: 20px;

  @media (min-width: @tablet-width) {
    font-size: 28px;
  }
}

.contacts__list {
  list-style: none;
  margin: 15px 0 0 0;
  padding: 0;
}

.contacts__link {
  font-weight: bold;
  color: @base;
  text-decoration: none;

  &:hover {
    color: @main;
  }

  &:active {
    color: fade(@base, 60%);
  }
}

.contacts__map {
  display: block;
  width: 100%;
  height: 240px;

  @media (min-width: @tablet-width) {
    height: 400px;
  }

  @media (min-width: @desktop-width) {
    height: 500px;
    max-width: 1200px;
    margin: 0 auto;
  }
}
