.clients {
  text-align: center;
  color: @main;
  padding: 20px 20px 10px;

  @media (min-width: @desktop-width) {
    padding-bottom: 70px;
  }
}

.clients__title {
  font-size: 22px;
  font-weight: bold;
  margin: 0 0 20px;

  @media (min-width: @tablet-width) {
    font-size: 28px;
  }
}

.clients__list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  justify-content: center;
  margin: 0;
  padding: 0;
}

.clients__item {
  width: 120px;
  padding: 10px;

  @media (min-width: @tablet-width) {
    width: 200px;
  }
}

.clients__image {
  width: 100%;
  object-fit: contain;
}
