.examples {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
  }

  @media (min-width: @desktop-width) {
    padding-bottom: 70px;
  }
}

.examples__title {
  color: @main;
  font-size: 28px;
  margin: 10px 0 30px;
}

.examples__list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  justify-content: center;
  margin: 0;
  padding: 0;
}

.examples__item {
  width: 45%;
  margin: 5px;

  @media (min-width: @desktop-width) {
    width: 30%;
  }
}

.examples__image {
  display: block;
  width: 100%;
  object-fit: contain;
}
