.page-footer {
  background-color: @main;
}

.page-footer__container {
  padding: 20px;
}

.page-footer__info {
  margin-bottom: 30px;

  @media (min-width: @tablet-width) {
    display: flex;
    font-size: 12px;
    align-items: flex-start;
    justify-content: space-between;
  }
}

.page-footer__text {
  @media (min-width: @tablet-width) {
    max-width: 25%;
  }
}

.page-footer__logo {
  text-align: center;
}

.page-footer__logo-link {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: @text-color;
  font-weight: bold;
  font-size: 18px;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.page-footer__logo-image {
  margin: 0 auto 10px;
}

.page-footer__contacts {
  text-decoration: none;
  color: @text-color;

  &:hover {
    color: @base;
  }
}
