.product {
  padding-top: 20px;
  padding-bottom: 20px;

  @media (min-width: @desktop-width) {
    padding-bottom: 70px;
  }
}

.product__title {
  display: block;
  text-align: center;
  color: @main;
  text-transform: uppercase;
  margin: 10px 0 30px;
  padding: 0;

  @media (min-width: @tablet-width) {
    font-size: 28px;
  }

  @media (min-width: @desktop-width) {
    font-size: 34px;
  }
}

.product__card {
  margin-bottom: 20px;

  @media (min-width: @desktop-width) {
    margin-bottom: 70px;
  }
}

.product__list {
  padding: 0;
  margin: 0;
  list-style: none;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
  }
}

.product__item {
  position: relative;
  text-align: center;
  border-radius: 10px;
  background-color: @base;

  &:not(:last-child) {
    margin-bottom: 20px;

    @media (min-width: @tablet-width) {
      margin: 0;
    }
  }

  &:hover {
    color: @base;
    background-color: @main;
    box-shadow: 0 0 20px @main;

    & .product__card-title {
      color: @base;
    }
  }

  &:active {
    opacity: 0.6;
    box-shadow: 0 0 20px @base;
  }
}

.product__item::before {
  content: "от 000р";
  position: absolute;
  top: 20px;
  left: 20px;
  font-weight: bold;
  color: @main;
  min-width: 70px;
  max-width: 85%;
  background-color: @base;
  border-radius: 7px;
  padding: 5px;
}

.product__link {
  text-decoration: none;
  color: @main;
}

.product__image {
  display: block;
  width: 100%;
  min-height: 200px;
  object-fit: cover;
  border-radius: 10px 10px 0 0;
}

.product__card-title {
  margin: 0;
  padding: 10px;
}

.product__see-more {
  text-decoration: none;
}
