.contacts-info__title {
  font-size: 18px;
  text-align: center;
  color: @main;

  @media (min-width: @tablet-width) {
    font-size: 28px;
  }
}

.contacts-info__wrapper {
  border-top: 1px solid @secondary;
  border-bottom: 1px solid @secondary;
  margin-bottom: 20px;

  @media (min-width: @tablet-width) {
    padding: 30px;
  }
}

.contacts-info__list {
  color: @main;
  list-style: none;
  font-size: 16px;
  margin: 0;
  padding: 0;

  @media (min-width: @tablet-width) {
    display: grid;
    max-width: 768px;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 80px;
    margin: 0 auto;
  }
}

.contacts-info__item {
  position: relative;
  padding: 0 0 0 65px;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 50px;
    height: 50px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;

    @media (min-width: @tablet-width) {
      top: 20px;
    }
  }

  @media (min-width: @desktop-width) {
    font-size: 18px;
  }
}

.contacts-info__item--address::before {
  background-image: url("../img/map.svg");
}

.contacts-info__item--phone::before {
  background-image: url("../img/phone.svg");
}

.contacts-info__item--email::before {
  background-image: url("../img/mail.svg");
}

.contacts-info__item--working::before {
  background-image: url("../img/working.svg");
}

.contacts-info__name {
  font-weight: bold;
}

.contacts-info__text a {
  text-decoration: none;
  color: @base;

  &:hover {
    color: @main;
    text-decoration: underline;
  }

  &:active {
    color: @main;
    opacity: 0.6;
  }
}
