.page-header {
  position: sticky;
  top: 0;
  z-index: 10;
  background-color: @main;
}

.page-header__container {
  display: flex;
  align-items: center;
}

.page-header__logo {
  margin-right: auto;

  &-link {
    display: flex;
    align-items: center;
    font-size: 24px;
    font-weight: bold;
    text-decoration: none;
    color: @text-color;
    padding: 10px 0;
  }

  @media (min-width: @desktop-width) {
    padding: 0 10px;
  }

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.page-header__logo-image {
  &:last-child {
    display: none;

    @media (min-width: @tablet-width) {
      display: block;
    }
  }

  @media (min-width: @tablet-width) {
    margin-right: 15px;
  }
}

.page-header__navigation {
  position: relative;
}

.page-header__navigation-toogle {
  position: absolute;
  width: 60px;
  height: 60px;
  top: -30px;
  right: 0;
  z-index: 2;
  background-color: transparent;
  border: none;
  cursor: pointer;

  &:active {
    opacity: 0.6;
  }

  @media (min-width: @desktop-width) {
    display: none;
  }
}

.page-header__navigation-toogle--closed::before {
  content: "";
  position: absolute;
  top: 20px;
  right: 0;
  width: 45px;
  height: 5px;
  background-color: @text-color;
  box-shadow:
    0 10px 0 @text-color,
    0 20px 0 @text-color;
  border-radius: 5px;
  transition-duration: 0.25s;
}

.page-header__navigation-toogle--opened {
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 30px;
    right: 0;
    width: 45px;
    height: 5px;
    background-color: @text-color;
    border-radius: 5px;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.page-header__navigation-list {
  display: none;

  @media (max-width: 1199px) {
    &--opened {
      position: absolute;
      display: flex;
      flex-direction: column;
      width: 100vw;
      list-style-type: none;
      top: 38px;
      right: -20px;
      z-index: 2;
      background-color: @main;
      margin: 0;
    }
  }

  @media (min-width: @tablet-width) {
    font-size: 18px;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
    font-size: 16px;
    max-width: 800px;
    list-style: none;
    margin: 0;
  }
}

.page-header__navigation-item {
  padding: 20px 0;

  @media (max-width: 1199px) {
    text-align: center;
    border-top: 2px solid darken(@secondary, 75%);
  }
}

.page-header__navigation-link {
  position: relative;
  font-weight: bold;
  text-decoration: none;
  text-transform: uppercase;
  color: @text-color;
  padding: 20px;

  &--current {
    color: fade(@base, 40%);
  }

  &:hover {
    color: @base;
  }

  &:active {
    opacity: 0.6;
  }

  @media (min-width: @desktop-width) {
    padding: 10px;
  }
}
