.intro {
  position: relative;
  background-color: @base;
  height: 150px;

  @media (min-width: @tablet-width) {
    height: 250px;
  }

  @media (min-width: @desktop-width) {
    height: 300px;
  }
}

.intro__wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 90%;
  height: 35px;
  font-size: 28px;
  font-weight: bold;
  line-height: 35px;
  color: @main;
  text-align: center;
  transform: translate(-50%, -50%);
  overflow: hidden;
  padding: 0;

  @media (min-width: @tablet-width) {
    height: 60px;
    font-size: 48px;
    font-weight: bold;
    line-height: 60px;
  }

  @media (min-width: @desktop-width) {
    height: 100px;
    font-size: 68px;
    font-weight: bold;
    line-height: 100px;
  }
}
