@font-face {
  font-family: "Open Sans";
  font-size: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-size: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-size: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
}
html {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0 auto;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #ffffff;
  background-color: #ffffff;
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 1200px) {
  .container {
    padding: 0;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  display: block;
  max-width: 400px;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  background-color: #ff8c00;
  color: #191919;
  border: 2px solid #191919;
  border-radius: 10px;
  padding: 10px;
  margin: 0 auto;
}
.button:hover {
  color: #ff8c00;
  background-color: #191919;
  border-color: #ff8c00;
}
@media (min-width: 1200px) {
  .button {
    font-size: 18px;
    padding: 10px;
  }
}
.breadcrumb {
  display: flex;
  list-style: none;
  margin: 20px 0 0;
  padding: 0;
}
.breadcrumb__item {
  margin-right: 5px;
}
.breadcrumb__link {
  text-decoration: none;
  color: #191919;
  list-style: none;
}
.breadcrumb__link--current {
  color: rgba(25, 25, 25, 0.5);
}
.breadcrumb__link:hover {
  text-decoration: underline;
}
.breadcrumb__link:active {
  color: rgba(25, 25, 25, 0.5);
}
.page-header {
  position: sticky;
  top: 0;
  z-index: 10;
  background-color: #191919;
}
.page-header__container {
  display: flex;
  align-items: center;
}
.page-header__logo {
  margin-right: auto;
}
.page-header__logo-link {
  display: flex;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
  text-decoration: none;
  color: #ffffff;
  padding: 10px 0;
}
@media (min-width: 1200px) {
  .page-header__logo {
    padding: 0 10px;
  }
}
.page-header__logo:hover {
  opacity: 0.6;
}
.page-header__logo:active {
  opacity: 0.3;
}
.page-header__logo-image:last-child {
  display: none;
}
@media (min-width: 768px) {
  .page-header__logo-image:last-child {
    display: block;
  }
}
@media (min-width: 768px) {
  .page-header__logo-image {
    margin-right: 15px;
  }
}
.page-header__navigation {
  position: relative;
}
.page-header__navigation-toogle {
  position: absolute;
  width: 60px;
  height: 60px;
  top: -30px;
  right: 0;
  z-index: 2;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.page-header__navigation-toogle:active {
  opacity: 0.6;
}
@media (min-width: 1200px) {
  .page-header__navigation-toogle {
    display: none;
  }
}
.page-header__navigation-toogle--closed::before {
  content: "";
  position: absolute;
  top: 20px;
  right: 0;
  width: 45px;
  height: 5px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 #ffffff, 0 20px 0 #ffffff;
  border-radius: 5px;
  transition-duration: 0.25s;
}
.page-header__navigation-toogle--opened::before,
.page-header__navigation-toogle--opened::after {
  content: "";
  position: absolute;
  top: 30px;
  right: 0;
  width: 45px;
  height: 5px;
  background-color: #ffffff;
  border-radius: 5px;
}
.page-header__navigation-toogle--opened::before {
  transform: rotate(45deg);
}
.page-header__navigation-toogle--opened::after {
  transform: rotate(-45deg);
}
.page-header__navigation-list {
  display: none;
}
@media (max-width: 1199px) {
  .page-header__navigation-list--opened {
    position: absolute;
    display: flex;
    flex-direction: column;
    width: 100vw;
    list-style-type: none;
    top: 38px;
    right: -20px;
    z-index: 2;
    background-color: #191919;
    margin: 0;
  }
}
@media (min-width: 768px) {
  .page-header__navigation-list {
    font-size: 18px;
  }
}
@media (min-width: 1200px) {
  .page-header__navigation-list {
    display: flex;
    flex-wrap: wrap;
    font-size: 16px;
    max-width: 800px;
    list-style: none;
    margin: 0;
  }
}
.page-header__navigation-item {
  padding: 20px 0;
}
@media (max-width: 1199px) {
  .page-header__navigation-item {
    text-align: center;
    border-top: 2px solid #2a2a2a;
  }
}
.page-header__navigation-link {
  position: relative;
  font-weight: bold;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  padding: 20px;
}
.page-header__navigation-link--current {
  color: rgba(255, 140, 0, 0.4);
}
.page-header__navigation-link:hover {
  color: #ff8c00;
}
.page-header__navigation-link:active {
  opacity: 0.6;
}
@media (min-width: 1200px) {
  .page-header__navigation-link {
    padding: 10px;
  }
}
.page-footer {
  background-color: #191919;
}
.page-footer__container {
  padding: 20px;
}
.page-footer__info {
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .page-footer__info {
    display: flex;
    font-size: 12px;
    align-items: flex-start;
    justify-content: space-between;
  }
}
@media (min-width: 768px) {
  .page-footer__text {
    max-width: 25%;
  }
}
.page-footer__logo {
  text-align: center;
}
.page-footer__logo-link {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: #ffffff;
  font-weight: bold;
  font-size: 18px;
}
.page-footer__logo-link:hover {
  opacity: 0.6;
}
.page-footer__logo-link:active {
  opacity: 0.3;
}
.page-footer__logo-image {
  margin: 0 auto 10px;
}
.page-footer__contacts {
  text-decoration: none;
  color: #ffffff;
}
.page-footer__contacts:hover {
  color: #ff8c00;
}
.page-main {
  flex: 1 1 auto;
}
.intro {
