.intro__wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 90%;
  height: 35px;
  font-size: 28px;
  font-weight: bold;
  line-height: 35px;
  color: #191919;
  text-align: center;
  transform: translate(-50%, -50%);
  overflow: hidden;
  padding: 0;
}
@media (min-width: 768px) {
  .intro__wrapper {
    height: 60px;
    font-size: 48px;
    font-weight: bold;
    line-height: 60px;
  }
}
