}
.contacts-info__wrapper {
  border-top: 1px solid #e9e9e9;
  border-bottom: 1px solid #e9e9e9;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .contacts-info__wrapper {
    padding: 30px;
  }
}
.contacts-info__list {
  color: #191919;
  list-style: none;
  font-size: 16px;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .contacts-info__list {
    display: grid;
    max-width: 768px;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 80px;
    margin: 0 auto;
  }
}
.contacts-info__item {
  position: relative;
  padding: 0 0 0 65px;
}
.contacts-info__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 50px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
@media (min-width: 768px) {
  .contacts-info__item::before {
    top: 20px;
  }
}
@media (min-width: 1200px) {
  .contacts-info__item {
    font-size: 18px;
  }
}
.contacts-info__item--address::before {
  background-image: url("../img/map.svg");
}
.contacts-info__item--phone::before {
  background-image: url("../img/phone.svg");
}
.contacts-info__item--email::before {
  background-image: url("../img/mail.svg");
}
.contacts-info__item--working::before {
  background-image: url("../img/working.svg");
}
.contacts-info__name {
  font-weight: bold;
}
.contacts-info__text a {
  text-decoration: none;
  color: #ff8c00;
}
.contacts-info__text a:hover {
  color: #191919;
  text-decoration: underline;
}
.contacts-info__text a:active {
  color: #191919;
  opacity: 0.6;
}
